'use client';
import React, { useEffect, useState } from "react";
import axios from "axios";

export default function ProductsPage() {
  const [products, setProducts] = useState([]);

  useEffect(() => {
    axios
      .get("http://localhost:5000/product/getall")
      .then((res) => setProducts(res.data))
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className="min-h-screen bg-slate-100 py-12 px-6">
      <div className="max-w-7xl mx-auto">
        {/* Page Heading */}
        <h1 className="text-4xl font-extrabold text-slate-900 mb-12 text-center">
          Our Products
        </h1>

        {/* Products Grid */}
        <div className="grid gap-10 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {products.map((product) => (
            <div
              key={product._id}
              className="group relative bg-white/80 backdrop-blur-lg border border-slate-200 shadow-md rounded-2xl overflow-hidden hover:shadow-xl hover:-translate-y-2 transition-all duration-300"
            >
              {/* Image Section */}
              <div className="h-60 bg-slate-200 flex items-center justify-center overflow-hidden">
                <img
                  src={product.images?.[0] || "https://via.placeholder.com/300"}
                  alt={product.name}
                  className="object-cover h-full w-full group-hover:scale-105 transition-transform duration-500"
                />
              </div>

              {/* Content */}
              <div className="p-6 flex flex-col gap-3">
                <h2 className="text-xl font-semibold text-slate-900 line-clamp-1">
                  {product.name}
                </h2>
                <p className="text-sm text-slate-600 line-clamp-2">
                  {product.description}
                </p>

                {/* Price + Stock */}
                <div className="flex justify-between items-center mt-2">
                  <span className="text-lg font-bold text-slate-800">
                    â‚¹{product.price}
                  </span>
                  <span
                    className={`text-sm font-medium ${
                      product.stock > 0
                        ? "text-green-600"
                        : "text-red-500"
                    }`}
                  >
                    {product.stock > 0 ? `${product.stock} in stock` : "Out of stock"}
                  </span>
                </div>

                {/* Buttons */}
                <div className="flex gap-3 pt-4">
                  <button className="flex-1 py-2 px-3 rounded-lg bg-slate-700 text-white font-medium hover:bg-slate-800 transition">
                    View
                  </button>
                  <button className="flex-1 py-2 px-3 rounded-lg bg-slate-200 text-slate-700 font-medium hover:bg-slate-300 transition">
                    Add to Cart
                  </button>
                </div>
              </div>

              {/* Decorative hover effect */}
              <div className="absolute inset-0 bg-gradient-to-br from-slate-50/0 via-slate-100/0 to-slate-200/0 group-hover:to-slate-200/20 transition-all"></div>
            </div>
          ))}

          {products.length === 0 && (
            <p className="text-center text-slate-500 col-span-full">
              No products available.
            </p>
          )}
        </div>
      </div>
    </div>
  );
}
